import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Spinner,
  UncontrolledAlert
} from "reactstrap";
import { login } from "../Auth";
import useInput from "../hooks/useInput";
import { toggle } from "../slices/authModal";
import { setTrue } from "../slices/CheckAgainForAuthentication";
export default function Authentication(props) {
  const [email, emailBind, emailReset] = useInput("");
  const [password, passwordBind, passwordReset] = useInput("");
  const [loading, setLoading] = useState(false);

  const [Failure, setFailure] = useState(false);
  const history = useHistory();
  const AuthModal = useSelector((state) => state.authModal.value);
  const dispatch = useDispatch();

  const toggleLogin = () => dispatch(toggle());
  const reset = () => {
    emailReset();
    passwordReset();
  };
  const onSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    login(email, password)
      .then((success) => {
        if (success) {
          toggleLogin();
          dispatch(setTrue());
        } else {
          setFailure(true);
        }
      })
      .catch(() => setFailure(true))
      .finally(() => {
        setLoading(false);
      });
  };
  const onCloseClick = () => {
    toggleLogin();
    reset();
    setFailure(false);
  };
  const close = (
    <button onClick={onCloseClick} className="btn btn-danger">
      X
    </button>
  );
  return (
    <Modal
      backdrop="static"
      className="modal-scrollable"
      isOpen={AuthModal}
      toggle={toggleLogin}
    >
      <ModalHeader
        className="bg-dark text-light modal-title goog-font"
        toggle={toggleLogin}
        close={close}
      >
        Enter your Login Credentials
        {loading && <Spinner />}
      </ModalHeader>
      <form onSubmit={onSubmit}>
        <ModalBody className="bg-dark text-light">
          <div className="my-2">
            {Failure && (
              <UncontrolledAlert
                finalize={() => setFailure(false)}
                color="danger"
              >
                Please enter correct credentials
              </UncontrolledAlert>
            )}
            <div>
              <input
                type="email"
                placeholder="Enter your email"
                {...emailBind}
                className="form-control my-3 py-2 text-dark"
                name="email"
                required
              />
              <input
                placeholder="Enter your password"
                className="form-control my-4 py-2"
                {...passwordBind}
                autoComplete="on"
                required
                type="password"
              />
            </div>
          </div>
        </ModalBody>
        <ModalFooter className="bg-dark text-light">
          <Button
            color="primary"
            onClick={() => {
              history.push("/");
            }}
          >
            Back to Home Page
          </Button>
          <Button color="success" onClick={reset}>
            Reset
          </Button>{" "}
          <Button type="submit" color="danger">
            Send
          </Button>
        </ModalFooter>
      </form>
    </Modal>
  );
}
